

import Vue from 'vue'
import Vuex from 'vuex'
import FastClick from 'fastclick'
import VueRouter from 'vue-router'
import App from './App'
import global_ from "./../config/global"
import Login from './components/Login'
import Home from './components/HelloFromVux'
import Reserve from './components/Reserve'
import Detail from './components/ReserveDetail'
import Cancel from './components/CancelReserve'
import WorkInfo from './components/WorkInfo'
import AjaxPlugin from 'vux/src/plugins/ajax/index.js'


Vue.use(AjaxPlugin)
Vue.use(VueRouter)
Vue.use(Vuex)


Vue.prototype.GLOBAL = global_;

const store = new Vuex.Store({
state: {
isLogin: 0,
userName:'',
empid:'',
deptid:'',
area:'',
roleFlag:'',
stuId:''
},
mutations: {
changeLogin (state,data) {
state.isLogin = data;
},
setUserName (state,data) {
state.userName = data;
},
setEmpId(state,data){
state.empid = data;
},
setDeptId(state,data){
state.deptid = data;
},
setArea(state,data){
state.area = data;
},
setRoleFlag(state,data){
state.roleFlag = data;
},
setStuId(state,data){
state.stuId = data;
}
}
})

const routes = [{
path: '/',
component: Login
},{
path: '/home',
meta:{auth:true}, // 设置当前路由需要校验  不需要校验的路由就不用写了
component: Home
},{
path: '/Reserve',
meta:{auth:true}, // 设置当前路由需要校验  不需要校验的路由就不用写了
component: Reserve
},{
path: '/Cancel',
meta:{auth:true}, // 设置当前路由需要校验  不需要校验的路由就不用写了
component: Cancel
},{
path: '/WorkInfo',
meta:{auth:true},
component: WorkInfo
},
{
path: '/Detail/:type/:date',
meta:{auth:true}, // 设置当前路由需要校验  不需要校验的路由就不用写了
component: Detail
}
]

const router = new VueRouter({
routes
})


router.beforeEach((to,from,next) => {

if(to.matched.some( m => m.meta.auth)){
var userInfo = JSON.parse(sessionStorage.getItem('userInfo'));

if(userInfo) { // 已经登陆
next()   
}
else{

next({path:'/',query:{ Rurl: to.fullPath} })
}
}
else{
next()
}
})

FastClick.attach(document.body)

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
router,
store,
render: h => h(App)
}).$mount('#app-box')



