import $ from 'jquery';
var curDate = new Date();
const list = () => [
dateFormat(new Date(), "MM/DD"),
dateFormat(new Date((curDate / 1000 + 86400) * 1000), "YYYY-MM-DD"),
dateFormat(new Date((curDate / 1000 + 86400 * 2) * 1000), "YYYY-MM-DD"),
dateFormat(new Date((curDate / 1000 + 86400 * 3) * 1000), "YYYY-MM-DD")
];

export default {
components: {
Group,Cell,XHeader,Tab,TabItem,Swiper,SwiperItem,InlineCalendar,Card,Icon,Grid,GridItem,GroupTitle,Divider
},
methods: {
onChange (val) {
},
onViewChange (val, count) {
console.log(val);
this.getBookdetailBetween(val.firstDate,val.lastDate);
},
getBookdetailBetween(firstDate,lastDate){
var that=this;
this.$http
.get(this.GLOBAL.BASE_URL+"rockBookdetail/getBookdetailBetween?empid="+JSON.parse(sessionStorage.getItem('userInfo')).empid+"&firstDate="+firstDate+"&lastDate="+lastDate)
.then(function(response) {
if(response.data.msg=="success"){
that.bookDetailList = response.data.data;
console.log(that.bookDetailList);
that.useCustomFn = true;
}else{

}
})
.catch(function(error) {
console.log('error'+error);
});
},
getMonthData(){
var that=this;
this.$http
.get(this.GLOBAL.BASE_URL+"rockBookdetail/getBookdetailMonth?empid="+JSON.parse(sessionStorage.getItem('userInfo')).empid+"&date="+this.value)
.then(function(response) {
if(response.data.msg=="success"){
const monthData = response.data.data;
that.monthTotal = monthData.monthAll;
that.monthTotalBai = monthData.monthBai;
that.monthTotalYe = monthData.monthYe;
}else{

}
})
.catch(function(error) {
console.log('error'+error);
});
},
getWeekData(){
var that=this;
this.$http
.get(this.GLOBAL.BASE_URL+"rockBookdetail/getBookdetailWeek?empid="+JSON.parse(sessionStorage.getItem('userInfo')).empid+"&date="+this.value)
.then(function(response) {
console.log(response.data);
if(response.data.msg=="success"){
const weekData = response.data.data;
if(weekData.weekTotal*1<that.weekSpec){
that.weekTotal = weekData.weekTotal;
that.showTitle = true;
}

}else{

}
})
.catch(function(error) {
console.log('error'+error);
});
}
},
data() {
return {
userName:'',
monthTotal:null,
monthTotalBai:null,
monthTotalYe:null,
bookDetailList:[],
list: list(),
weekTotal:null,
weekSpec:4,
showTitle:false,
show: true,
buildSlotFn: () => '',
value: dateFormat(new Date(), "YYYY-MM-DD"),
listValue: '',
range: false,
showLastMonth: true,
showNextMonth: true,
highlightWeekend: false,
return6Rows: true,
hideHeader: false,
hideWeekList: false,
replaceTextList: {},
replace: false,
changeWeeksList: false,
useCustomFn: false,
disablePast: true,
disableFuture: true,
disableWeekend: false
};
},
created:function(){
if(this.$store.state.userName!=""){
this.userName = this.$store.state.userName;
}
else{
this.userName=JSON.parse(sessionStorage.getItem('userInfo')).empname
}

this.getMonthData();
this.getWeekData();
},
computed:{

},
