useCustomFn (val) {
this.buildSlotFn = val ? (line, index, data) => {
var date = data.formatedDate;
var result = '';
this.bookDetailList.forEach(element => {
if(dateFormat(element.date, "YYYY-MM-DD")==date){
if(element.shiftflag==1){
result='<div style="font-size:8px;text-align:center;color:#FFF""><span style="display:inline-block;width:20px;height:20px;background-color:green;">白</span></div>';
}
else{
result= '<div style="font-size:8px;text-align:center;color:#FFF"><span style="display:inline-block;width:20px;height:20px;background-color:red;">夜</span></div>'
}
}
});
return result;
