
import XHeader from 'vux/src/components/x-header/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import Card from 'vux/src/components/card/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Box from 'vux/src/components/box/index.vue'
import Calendar from 'vux/src/components/calendar/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Popup from 'vux/src/components/popup/index.vue'

export default {
name: "WorkInfo",
components: {
XHeader,Box,
Actionsheet,
ButtonTab,
ButtonTabItem,
Grid,
GridItem,
GroupTitle,Cell,
Divider,
Badge,
Card,
XTextarea,
XInput,
Group,Calendar,
dateFormat,
XButton,Toast,Popup
},
directives: {
TransferDom
},
methods: {
onItemClick(key){
this.show = true;
this.currentKey = key;
},
onItemClick1() {

var that = this;
console.log(this.reasonForCancel);
this.$http
.get(
"http://localhost/rockBookdetail/cancelReserveById?id=" +this.currentKey+"&reasonForCancel="+this.reasonForCancel
)
.then(function(response) {
if (response.data.msg == "success") {
let result = response.data.data;
if(result==1){
that.showPositionValue = true
that.reasonForCancel='';
that.getReserveFuture();
that.show=false;
}
} else {
that.resultTitle = response.data.msg;
that.showPositionValue = true
}
})
.catch(function(error) {
console.log("error" + error);
});
},
getReserveFuture(){
var that = this;
this.$http
.get(
"http://localhost/rockBookdetail/getReserveFuture?empid=" +
JSON.parse(sessionStorage.getItem("userInfo")).empid
)
.then(function(response) {
if (response.data.msg == "success") {
that.bookDetailFutureList = response.data.data;
that.bookDetailFutureList.forEach(element => {
element.date = dateFormat(element.date,'YYYY-MM-DD');
});
} else {
}
})
.catch(function(error) {
console.log("error" + error);
});
}
},
created: function() {
this.userName = this.$store.state.userName;
this.userId = this.$store.state.userId;
this.getReserveFuture();
},
mounted: function() {
this.dividerTitle =
"日期:&nbsp;" +
this.date +
"&nbsp;&nbsp;&nbsp;班次:&nbsp;" +
this.dayornight;
},
watch: {
$route(to, from) {

console.log(from.data);
}
},
data() {
return {
userName: "",
userId:"",
demo2: 'TODAY',
msg: "1.好好工作好好干活。2.多干活多挣钱。3.不要太累了哟",
shiftFlag: this.$route.params.type,
dayornight: this.$route.params.type == "1" ? "白班" : "夜班",
date: this.$route.params.date,
workstationInfoList: [],
bookDetailFutureList:[],
dividerTitle: "",
resultTitle:"取消预约成功！",
userName: "",
position: 'default',
showPositionValue: false,
show: false,
reasonForCancel:'',
currentKey:''
};
}
};



