
import XHeader from 'vux/src/components/x-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import XTable from 'vux/src/components/x-table/index.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import Card from 'vux/src/components/card/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'

var curDate = new Date();
const list = () => [
dateFormat(new Date(), "MM-DD"),
dateFormat(new Date((curDate / 1000 + 86400) * 1000), "MM-DD"),
dateFormat(new Date((curDate / 1000 + 86400 * 2) * 1000), "MM-DD"),
dateFormat(new Date((curDate / 1000 + 86400 * 3) * 1000), "MM-DD")
];
export default {
name: "Reserve",
components: {
XHeader,
Tab,
XTable,
TabItem,
Swiper,
SwiperItem,
Cell,
Actionsheet,
ButtonTab,
ButtonTabItem,
Grid,
GridItem,
GroupTitle,
Divider,
Badge,
Card,
XTextarea,
XInput,
Group,
dateFormat
},
methods: {
onItemClick() {
alert("on item click");
},
itemClick() {
let date = new Date().getFullYear() + "-" + this.list[this.index];
date = dateFormat(date, "YYYY-MM-DD");
let deptid = JSON.parse(sessionStorage.getItem('userInfo')).deptid;

this.baiWorkstationInfoList.splice(0,this.baiWorkstationInfoList.length);
this.yeWorkstationInfoList.splice(0,this.yeWorkstationInfoList.length);
this.getDailyWorkstation();


this.dayUrl = "/Detail/1/" + date;
this.nightUrl = "/Detail/2/" + date;
},
getDailyWorkstation(){
var that=this;
let date_ = new Date().getFullYear() + "-" + this.list[this.index];
date_ = dateFormat(date_, "YYYY-MM-DD");
this.$http
.get(this.GLOBAL.BASE_URL+"rockDailyworkstation/getDailyWorkstation?deptid="+JSON.parse(sessionStorage.getItem('userInfo')).deptid+"&date="+date_)
.then(function(response) {
if(response.data.msg=="success"){
const WorkstationInfoList = response.data.data;
that.baiWorkstationInfoList.splice(0,that.baiWorkstationInfoList.length);
that.yeWorkstationInfoList.splice(0,that.yeWorkstationInfoList.length);
WorkstationInfoList.forEach(workstationInfo => {
let tempBai = {area:workstationInfo.area,count:workstationInfo.daynumber-workstationInfo.bookeddaynumber};
let tempYe = {area:workstationInfo.area,count:workstationInfo.nightnumber-workstationInfo.bookednightnumber};
that.baiWorkstationInfoList.push(tempBai);
that.yeWorkstationInfoList.push(tempYe);
});
console.log(that.baiWorkstationInfoList);
console.log(that.yeWorkstationInfoList);
}else{

}
})
.catch(function(error) {
console.log('error'+error);
});
}
},
created: function() {
this.userName = this.$store.state.userName;
this.getDailyWorkstation();
},
mounted: function() {
let date = dateFormat(new Date(), "YYYY-MM-DD");
this.dayCount = "RPL余" + date.substr(-1) + "人，FIL余1人";
this.nightCount = "RPL余" + date.substr(-1) + "人，FIL余1人";
this.dayUrl = "/Detail/1/" + date;
this.nightUrl = "/Detail/2/" + date;
this.dividerTitle =
"日期:&nbsp;" +
this.date +
"&nbsp;&nbsp;&nbsp;班次:&nbsp;" +
this.dayornight;
},
watch: {
$route(to, from) {

console.log(from.data);
}
},
data() {
return {
index: 0,
value: dateFormat(new Date(), "YYYY-MM-DD"),
dayUrl: "",
nightUrl: "",
list: list(),
demo2: "",

dayCount: "",
nightCount: "",
msg: "1.好好工作好好干活。2.多干活多挣钱。3.不要太累了哟",
dayornight: this.$route.params.type == "0" ? "白班" : "夜班",
date: this.$route.params.date,
dividerTitle: "",
userName: "",
baiWorkstationInfoList:[
],
yeWorkstationInfoList:[
],
stafflist: [
{ name: "张三", id: "50109479" },
{ name: "李四", id: "50109478" },
{ name: "王五", id: "50109477" }
],
stafflist1: [
{ name: "赵六", id: "50109476" },
{ name: "田七", id: "50109475" },
{ name: "孙八", id: "50109474" }
]
};
}
};



