

import GroupTitle from 'vux/src/components/group-title/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import { createECDH } from 'crypto';
export default {
name: "Login",
components: { GroupTitle, Group, Cell, XInput, XButton, Box,Toast   },
data() {
return {
empid: "",
password:"",
msg: "上班管家",
errmsg:"请输入工号密码"
};
},
methods: {
login: function() {
var thisRouter = this.$router;
var thisStore = this.$store;
this.$http
.get(this.GLOBAL.BASE_URL+"rockOpemp/login?empid="+this.empid+"&password="+this.password)
.then(function(response) {
console.log(response.data);
if(response.data.msg=="success"){
const userInfo = response.data.data;
sessionStorage.setItem('userInfo', JSON.stringify(userInfo));
thisStore.commit('changeLogin','1');
thisStore.commit('setUserName',response.data.data.empname);
thisStore.commit('setEmpId',response.data.data.empid);
thisStore.commit('setDeptId',response.data.data.deptid);
thisStore.commit('setArea',response.data.data.area);
thisStore.commit('setRoleFlag',response.data.data.roleflag);
thisStore.commit('setStuId',response.data.data.stuid);
thisRouter.push({ path: 'home' });
console.log(thisStore)
}else{

}
})
.catch(function(error) {
console.log('error'+error);
});
}
}
};



