
import XHeader from 'vux/src/components/x-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import Card from 'vux/src/components/card/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'
import Toast from 'vux/src/components/toast/index.vue'
import Alert from 'vux/src/components/alert/index.vue'

import Vue from 'vue';
export default {
name: "ReserveDetail",
components: {
XHeader,
Actionsheet,
ButtonTab,
ButtonTabItem,
Grid,
GridItem,
GroupTitle,
Divider,
Badge,
Card,
XTextarea,
XInput,
Group,
dateFormat,Toast,Alert
},
methods: {
onItemClick(shiftFlag,area) {

let this_roleflag = JSON.parse(sessionStorage.getItem("userInfo")).roleflag;
if(this_roleflag==3){
this.alertTitle="抱歉";
this.alertContent="当前您还处于学徒期，无法独自操作，可联系师傅进行预约"
this.showAlert = true;
}else if(this_roleflag==2){
let this_stuId = JSON.parse(sessionStorage.getItem("userInfo")).stuid;
if(this_stuId){
debugger;
}
else{
this.alertTitle="抱歉";
this.alertContent="未获取到学徒信息，请联系科室管理员确认师徒绑定关系"
this.showAlert = true;
}
}else{

}
},
reserve(date,empid,area,stuid,shiftflag){
var that = this;
this.$http
.get(
this.GLOBAL.BASE_URL+"rockBookdetail/reserve?empid="+empid+"&date="+date+"&area="+area+"&stuid="+stuid+"&shiftflag="+shiftflag
)
.then(function(response) {
if (response.data.msg == "success") {

} else {
this.alertTitle="抱歉";
this.alertContent=response.data.msg;
this.showAlert = true;
}
})
.catch(function(error) {
console.log("error" + error);
});
},
getDailyWorkstation(shiftFlag) {
var that = this;
this.$http
.get(
this.GLOBAL.BASE_URL+"rockDailyworkstation/getDailyWorkstation?deptid=" +
JSON.parse(sessionStorage.getItem("userInfo")).deptid +
"&date=" +
this.date
)
.then(function(response) {
if (response.data.msg == "success") {
const WorkstationInfoList = response.data.data;


WorkstationInfoList.forEach(workstationInfo => {
let date = dateFormat(workstationInfo.date, "YYYY-MM-DD");
that.getBookdetailDay(workstationInfo,shiftFlag,JSON.parse(sessionStorage.getItem("userInfo")).deptid,workstationInfo.area,date)
});
that.workstationInfoList = WorkstationInfoList;
} else {
}
})
.catch(function(error) {
console.log("error" + error);
});
},
getBookdetailDay(workstationInfo,shiftFlag,deptid,area,date){
var  userList = [];
this.$http.get(this.GLOBAL.BASE_URL+"rockBookdetail/getBookdetailDay?deptid=" +deptid +"&area="+area+"&date=" +date).then(function(response){
if(response.data.msg == "success"){
const users = response.data.data;
for(let i=0;i<users.length;i++){
let tempUser=new Object();
tempUser.empName = users[i].empName;
tempUser.empId = users[i].empid;
userList.push(tempUser);
}
Vue.set(workstationInfo, 'userList', userList)
}else{

}
})
.catch(function(error){
console.log(error);
}
);
}
},
created: function() {
this.userName = this.$store.state.userName;
},
mounted: function() {
this.getDailyWorkstation(this.shiftFlag);
this.dividerTitle =
"日期:&nbsp;" +
this.date +
"&nbsp;&nbsp;&nbsp;班次:&nbsp;" +
this.dayornight;
},
watch: {
$route(to, from) {

console.log(from.data);
}
},
data() {
return {
msg: "1.好好工作好好干活。2.多干活多挣钱。3.不要太累了哟",
shiftFlag: this.$route.params.type,
dayornight: this.$route.params.type == "1" ? "白班" : "夜班",
date: this.$route.params.date,
workstationInfoList: [],
dividerTitle: "",
userName: "",
showAlert:false,
alertTitle:"",
alertContent:""
};
}
};



